        refreshStateBySelectedFile(state, _newDict) {
            let that = this;
            that.state.spells = _newDict.spells;
            that.state.words = _newDict.words;
            that.state.selectAllWords = _newDict.selectAllSwitchStatus;
            that.state.selectedWords = _newDict.selectedWords;
        },
        updateScaleCoef(state, _newValue) {
            let that = this;
            that.state.scaleCoef = _newValue.value;
        },
        updateDuplicate(state, _newValue) {
            let that = this;
            that.state.duplicate = _newValue.value;
        },
        updateSelectedWords(state, _newValue) {
            let that = this;
            that.state.selectedWords[_newValue.key] = _newValue.value;
        },
        updateSelectAllWordsSwitchStatus(state, _newValue) {
            let that = this;
            that.state.selectAllWords[_newValue.key] = _newValue.value;
        },
        deleteSelectedWordByContent(state, _input) {
            let that = this;
            let index = that.state.selectedWords[_input.recordName].indexOf(_input.content);
            that.state.selectedWords[_input.recordName].splice(index, 1);
        }
