<template>
	<div style="margin: 20px">
		<a-cascader :options="poemInfoFiltered" placeholder="Please select" @change="selectPoem" :autoClear="false"/>
		<a-radio-group default-value="词格式" style="margin: 30px">
			<a-radio-button value="诗格式" @click="selectFormat">诗格式</a-radio-button>
			<a-radio-button value="词格式" @click="selectFormat">词格式</a-radio-button>
		</a-radio-group>
		<input type="file" id="poemFileInput" style="width: 200px"/>
		<a-button @click="uploadPoemFile">使用File</a-button>
		<a-button @click="downloadPoemFile">下载File</a-button>
		<!--看字写拼音-->
		<div :style="cardBackDiv">
			<a-card title="看字写拼音" :style="sectionStyle">
				<a-row>
					<span v-for="(item, i) in (selectedPoemName.length + selectedPoemAuthor.length + selectedPoemEra.length + 2)" v-bind:key="i">
						<span class="WordElement" style="float:left" :style="blackboard">
							<a-row :style="spellClass"></a-row>
							<a-row :style="wordClass">
								<span>{{(selectedPoemName+" "+selectedPoemEra+" "+selectedPoemAuthor)[i] }}</span>
							</a-row>
						</span>
					</span>
				</a-row>
				<span v-for="(item, i) in maximumElementQuant" v-bind:key="i">
					<span class="WordElement" style="float:left" :style="blackboard">
						<a-row :style="spellClass">{{ spellsCache[i] }}</a-row>
						<a-row :style="wordClass">
							<span>{{ characters[i] }}</span>
						</a-row>
					</span>
				</span>

			</a-card>
		</div>
	</div>
</template>

<script>
function uniqueArray(_inputArray){
    let res = [];
    let _length = _inputArray.length;
    for (let i=0;i<_length;i++){
        if (res.indexOf(_inputArray[i]) === -1){
            res.push(_inputArray[i])
        }
    }
    return res;
}
/* eslint no-mixed-spaces-and-tabs: ["error", "smart-tabs"] */
/* eslint no-unused-vars: [0] */
export default {
    name: "poemStudy",
    data() {
        return {
            spellsCache: [],
            selectedPoemName: "七律·长征",
            selectedPoemAuthor: "毛泽东",
            selectedPoemEra: "现代",
            selectedPoemContent: "-",
            selectedFormat: "词格式",
            sectionStyle: {width: '100%'},
            characters: "M",
            maximumElementQuant: 0,
        }
    },
    methods: {
        downloadPoemFile: function () {
            let that = this;
            let _poems = JSON.stringify(that.$store.getters.getPoemPackages);
            let _blob = new Blob([_poems]);
	        let element = document.createElement("a");
            element.href = window.URL.createObjectURL(_blob);
            element.download = "poems.json";
            element.click();
            element.remove()
        },
        selectPoem: function (e) {
            let cache = e.length === 2;
            let author = e[0];
            let title = e[1];
	        let that = this;
	        // that.selectedPoemName = "";
	        // that.selectedPoemAuthor = "";
	        // that.selectedPoemEra = "";
	        // that.selectedPoemContent = "";
	        // that.characters = "";
	        // that.maximumElementQuant = 0;
	        if (cache){
	            let _titles = that.$store.getters.getPoemAttr("name");
	            let _authors = that.$store.getters.getPoemAttr("author");
	            let _eras = that.$store.getters.getPoemAttr("era");
	            let _contents = that.$store.getters.getPoemAttr("content");
	            that.selectedPoemName = title;
	            let _locInNames = _titles.indexOf(title);
	            that.selectedPoemAuthor = _authors[_locInNames];
	            that.selectedPoemEra = _eras[_locInNames];
	            that.selectedPoemContent = _contents[_locInNames];
	        }
        },
        selectFormat: function (e) {
            let that = this;
            that.selectedFormat = e.target._value;
        },
        uploadPoemFile: function () {
            let that = this;
            let myFile = document.querySelector("#poemFileInput")
            let reader = new FileReader();
            reader.readAsText(myFile.files[0])
            reader.onload = function () {
                let _uploadContent = eval(reader.result);
                console.log(_uploadContent)
	            that.$store.commit("refreshPoemsBySelectedFile", {"value": _uploadContent})

            }
        }
    },
    watch: {
        selectedFormat: function (_new, _old) {
            let that = this;
            if (_new === "诗格式") {
                let quant = that.characters.indexOf("。");
                let cache = document.querySelector(".WordElement").style.width;
                let eachWidth = parseInt(cache.replace("px", ""));
                that.sectionStyle = {width: (quant + 2) * (eachWidth + 2) + 'px'};
            } else {
                that.sectionStyle = {width: '100%'}
            }
        },
        selectedPoemContent: function(_new, _old){
            let that = this;
            that.characters = _new.split("")
            that.maximumElementQuant = _new.split("").length
        }
    },
    computed: {
        poemInfoFiltered: function () {
            let that = this;
            let _titles = that.$store.getters.getPoemAttr("name");
            let _authors = that.$store.getters.getPoemAttr("author");
            let _authors_unique = uniqueArray(_authors);
            let _authors_titles = Array(_authors_unique.length);
            for (let i=0;i<_authors_unique.length;i++){
                _authors_titles[i] = [];
            }
            for (let i=0;i<_authors.length;i++){
                let _loc = _authors_unique.indexOf(_authors[i]);
                _authors_titles[_loc].push(_titles[i]);
            }
            let res = Array(_authors_unique.length);
            for (let i=0;i<_authors_unique.length;i++){
                res[i] = [];
            }
            for (let i=0;i<_authors_unique.length;i++){
                let _children = [];
                for (let j=0;j<_authors_titles[i].length;j++){
                    let cache = {value: _authors_titles[i][j], label: _authors_titles[i][j]};
                    _children.push(cache);
                }
                res[i] = {label: _authors_unique[i], value: _authors_unique[i], children: _children}
            }
            return res;
        },
        blackboard: function () {
            return {
                backgroundImage: "url(" + require('../assets/metaTable.png') + ")",
                width: 100 * this.$store.getters.getScaleCoef + "px",
                height: 141 * this.$store.getters.getScaleCoef + "px",
                backgroundSize: "100%",
                backgroundRepeat: "no-repeat"
            }
        },
        spellClass: function () {
            return {
                "height": 33 * this.$store.getters.getScaleCoef + "px",
                "lineHeight": 20 * this.$store.getters.getScaleCoef + "px",
                "textAlign": "center",
                "verticalAlign": "middle",
                "fontSize": 26 * this.$store.getters.getScaleCoef + "px"
            }
        },
        wordClass: function () {
            return {
                "height": 95 * this.$store.getters.getScaleCoef + "px",
                "lineHeight": 95 * this.$store.getters.getScaleCoef + "px",
                "textAlign": "center",
                "verticalAlign": "middle",
                "fontSize": 40 * this.$store.getters.getScaleCoef + "px",
                // "fontFamily": "url(" + require('Comic Sans MS') + ")",
            }
        },
        cardBackDiv: function () {
            return {
                background: "gainsboro",
                marginTop: 24 * this.$store.getters.getScaleCoef + "px",
                marginBottom: 24 * this.$store.getters.getScaleCoef + "px",
                padding: 24 * this.$store.getters.getScaleCoef + "px",
            }
        },
    },
    filters: {
    }
}
</script>

